
$메인칼라 : #b28dcf;

.turn-enter{
    opacity: 0;
}
.turn-enter-active{
    opacity: 1;
    transition: all 800ms;

}



div.container{
    text-align: center;
}
div.container .row{
    h4{
        color : #b28dcf;
    }
    p{
        color : green;
    }
}
@mixin 함수(){
   
    padding : 20px;
    border-radius: 5px;
    max-width:500px;
    width : 100%;
    block-size: 200px;
    background-image: url('./alert.jpg');
    

}
.my-alert{
    @include 함수()
}
.my-alert p{
    margin-bottom: 0;
}
