.App {
  text-align: center;
  background-color: rgb(219, 213, 230);
}
.black-nav {
  background : black;
  width : 100%;
  display : flex;
  color : white;
  padding : 20px;
  font-weight : 600;
  font-size : 20px;

}
.link{
  color :white;
}
.subtitle{
  background-color: rgb(210, 200, 200);
  padding : 10px;
  margin: 10px;
  opacity: 70px;
}

.modal {
  margin-top: 20px;
  padding : 20px;
  background: rgb(167, 161, 161) ;
}

.list {
  margin-top: 30px;
  text-align: left;
  padding-left: 20px;
  padding-right: 20px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
.publish {
  margin-top : 30px;
  margin-bottom : 30px;
}
.interview {
  background-image: url('./ill.jpg');
  color : rgb(175, 204, 44);
  background-size: auto;
}

.background{
  background-image: url('./sky.jpg');
  color : white;
  background-size: cover;
  padding: 10px;
}
.publish input {
  padding : 10px;
  font-size : 16px;
  border-radius : 5px;
  width : 80%;
  border : 1px solid grey;
}
.publish button {
  display : block;
  margin : auto;
  margin-top : 10px;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
